<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <div class="card-deck">
      <div v-for="gamer in gamerData" :key="gamer.pid">
        <GamerCard :gamer="gamer" />
      </div>
    </div>
  </div>
</template>

<script>
import GamerCard from './GamerCard.vue';
import axios from 'axios';
export default {
  name: 'BoS',
  components: {
    GamerCard
  },
  props: {
    msg: String
  },
  data(){
    return {
      pids: [
        '52fc0217-88e8-4f2e-9763-5d1da29ab6df', // xchu`kx
        'a16ba01c-2115-47f6-ba5f-b70af8e7cc14', // coldfumonkeh
        '97476967-4a4d-4841-8694-7df5cca12564', // RMDavis
        '37908b65-01d7-4f40-9608-b601fce2d9ae' // SJ Raw
      ],
      gamerData: []
    }
  },
  created() {
    this.pidLoop();
  },

  methods: {

    pidLoop() {
      var self = this;
      var i;
      for( i = 0; i < self.pids.length; i++ ){
        this.getGamerData( self.pids[ i ] );
      }
    },

    getGamerData( pid ){
      axios
        .get( 'https://thedivisiontab.com/api/player.php?pid=' + pid )
        .then( response => this.gamerData.push( response.data ) );
    }

  }
}
</script>

